@import url(https://fonts.googleapis.com/css?family=Abel);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-link {
  color: #61dafb;
}

.navbar-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 4vmin;
  pointer-events: none;
  margin-right: 20px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.product-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
}

.product-card {
  font-family: Abel, Arial, Verdana, sans-serif;
  width: 500px;
  height: 250px;
  background-color: #fff;
  background: -webkit-gradient(linear, left top, left bottom, from(#f8f8f8), to(#fff));
  background: linear-gradient(#f8f8f8, #fff);
  box-shadow: 0 8px 16px -8px rgba(0, 0, 0, 0.4);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
  margin: 1.5rem;
}

.product-card h1 {
  text-align: center;
}

.product-card .product-additional {
  position: absolute;
  width: 150px;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#de685e), to(#ee786e));
  background: linear-gradient(#de685e, #ee786e);
  -webkit-transition: width 0.4s;
  transition: width 0.4s;
  overflow: hidden;
  z-index: 2;
}

.product-card.green .product-additional {
  background: -webkit-gradient(linear, left top, left bottom, from(#92bca6), to(#a2ccb6));
  background: linear-gradient(#92bca6, #a2ccb6);
}

.product-card:hover .product-additional {
  width: 100%;
  border-radius: 0 5px 5px 0;
}

.product-card .product-additional .product-img-card {
  width: 150px;
  height: 100%;
  position: relative;
  float: left;
}

.product-card .product-additional .product-img-card::after {
  content: "";
  display: block;
  position: absolute;
  top: 10%;
  right: -2px;
  height: 80%;
  border-left: 2px solid rgba(0, 0, 0, 0.025);
}

.product-card .product-additional .product-img-card .product-type,
.product-card .product-additional .product-img-card .product-status {
  top: 15%;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.15);
  padding: 0.125rem 0.75rem;
  border-radius: 100px;
  white-space: nowrap;
}

.product-card .product-additional .product-img-card .product-img {
  top: 50%;
}

.product-card .product-additional .product-img-card .product-status {
  top: 85%;
}

.product-card .product-additional .product-img-card i {
  display: block;
  color: #fff;
  font-size: 4rem;
  top: 50%;
}

.product-card .product-additional .product-more-info {
  width: 350px;
  float: left;
  position: absolute;
  left: 150px;
  height: 100%;
}

.product-card .product-additional .product-more-info h1 {
  color: #fff;
  margin-bottom: 0;
}

.product-card.green .product-additional .product-more-info h1 {
  color: #224c36;
}

.product-card .product-additional .product-coords {
  margin: 0 1rem;
  color: #fff;
  font-size: 1rem;
}

.product-card.green .product-additional .product-coords {
  color: #325c46;
}

.product-card .product-additional .product-evolution {
  float: left;
  width: 33.33%;
  font-size: 1rem;
  text-align: center;
}

.product-card .product-additional .product-stats {
  font-size: 2rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  right: 1rem;
  top: auto;
  color: #fff;
}

.product-card.green .product-additional .product-stats {
  color: #325c46;
}

.product-card .product-additional .product-stats > div {
  flex: 1 1;
  text-align: center;
}

.product-card .product-additional .product-stats i {
  display: block;
}

.product-card .product-additional .product-stats div.title {
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
}

.product-card .product-additional .product-stats div.value {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.product-card .product-additional .product-stats div.value.infinity {
  font-size: 2.5rem;
}

.product-card .product-general {
  width: 350px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  box-sizing: border-box;
  padding: 1rem;
  padding-top: 0;
}

.product-card .product-general .product-more {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  font-size: 0.9em;
}

